import * as layout from "../../routes/$layout.js";

const components = [
	() => import("../../routes/index.js"),
	() => import("../../routes/checkout.js"),
	() => import("../../routes/kontakt.js"),
	() => import("../../routes/about.js"),
	() => import("../../routes/boty/index.js"),
	() => import("../../routes/boty/[slug].js")
];

export const pages = (d => [
	{
		// index.svelte
		pattern: /^\/$/,
		parts: [
			[components[0]]
		]
	},

	{
		// checkout.svelte
		pattern: /^\/checkout\/?$/,
		parts: [
			[components[1]]
		]
	},

	{
		// kontakt.svelte
		pattern: /^\/kontakt\/?$/,
		parts: [
			[components[2]]
		]
	},

	{
		// about.svelte
		pattern: /^\/about\/?$/,
		parts: [
			[components[3]]
		]
	},

	{
		// boty/index.svelte
		pattern: /^\/boty\/?$/,
		parts: [
			[components[4]]
		]
	},

	{
		// boty/[slug].svelte
		pattern: /^\/boty\/([^/]+?)\/?$/,
		parts: [
			[components[5], m => ({ slug: d(m[1]) })]
		]
	}
])(decodeURIComponent);

export const ignore = [
	
];

export { layout };